
// THREE START
global.THREE = require('three');
const createLoop = require('raf-loop');
const EffectComposer = require('three-effectcomposer')(THREE);
const glslify = require('glslify');


//import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass'
//import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass'
//import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer'
// three js
const scene = new THREE.Scene();
const camera = new THREE.OrthographicCamera(-400, 400, 225, -225);
const renderer = new THREE.WebGLRenderer();
const videoContainer = document.getElementById('three-container');
console.log(videoContainer)
videoContainer.appendChild(renderer.domElement);
renderer.setSize(800, 450);
const video = document.getElementById('video');

const geometry = new THREE.PlaneGeometry(800, 450);
const texture = new THREE.VideoTexture(video);
texture.minFilter = THREE.LinearFilter
const material = new THREE.MeshBasicMaterial({
  color: 0xffffff,
  map: texture,
});
const mesh = new THREE.Mesh(geometry, material);
scene.add(mesh);
//TODO: add light!
mesh.position.z = -1;

const target = new THREE.WebGLRenderTarget(800, 450);
target.texture.stencil = false;
target.texture.minFilter = THREE.LinearFilter;
target.texture.magFilter = THREE.LinearFilter;
target.texture.format = THREE.RGBFormat;
target.texture.generateMipmaps = false;

const composer = new EffectComposer(renderer, target);

composer.addPass(new EffectComposer.RenderPass(scene, camera));


const lut = new EffectComposer.ShaderPass({
  vertexShader: glslify(["#define GLSLIFY 1\n\n  varying vec2 vUv;\n  void main() {\n    vUv = uv;\n    gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);\n  }\n  "]),
  fragmentShader: glslify(["\n  precision mediump float;\n#define GLSLIFY 1\n\n  #define LUT_FLIP_Y\n\n  varying vec2 vUv;\n  uniform sampler2D tDiffuse;\n  uniform sampler2D tLookup;\n\n  vec4 lookup(in vec4 textureColor, in sampler2D lookupTable) {\n    #ifndef LUT_NO_CLAMP\n        textureColor = clamp(textureColor, 0.0, 1.0);\n    #endif\n\n    mediump float blueColor = textureColor.b * 63.0;\n\n    mediump vec2 quad1;\n    quad1.y = floor(floor(blueColor) / 8.0);\n    quad1.x = floor(blueColor) - (quad1.y * 8.0);\n\n    mediump vec2 quad2;\n    quad2.y = floor(ceil(blueColor) / 8.0);\n    quad2.x = ceil(blueColor) - (quad2.y * 8.0);\n\n    highp vec2 texPos1;\n    texPos1.x = (quad1.x * 0.125) + 0.5/512.0 + ((0.125 - 1.0/512.0) * textureColor.r);\n    texPos1.y = (quad1.y * 0.125) + 0.5/512.0 + ((0.125 - 1.0/512.0) * textureColor.g);\n\n    #ifdef LUT_FLIP_Y\n        texPos1.y = 1.0-texPos1.y;\n    #endif\n\n    highp vec2 texPos2;\n    texPos2.x = (quad2.x * 0.125) + 0.5/512.0 + ((0.125 - 1.0/512.0) * textureColor.r);\n    texPos2.y = (quad2.y * 0.125) + 0.5/512.0 + ((0.125 - 1.0/512.0) * textureColor.g);\n\n    #ifdef LUT_FLIP_Y\n        texPos2.y = 1.0-texPos2.y;\n    #endif\n\n    lowp vec4 newColor1 = texture2D(lookupTable, texPos1);\n    lowp vec4 newColor2 = texture2D(lookupTable, texPos2);\n\n    lowp vec4 newColor = mix(newColor1, newColor2, fract(blueColor));\n    return newColor;\n}\n\n  void main () {\n    gl_FragColor = texture2D(tDiffuse, vUv);\n    gl_FragColor.rgb = lookup(gl_FragColor, tLookup).rgb;\n  }\n"]),
  uniforms: {
    tDiffuse: { type: 't', value: new THREE.Texture() },
    tLookup: { type: 't', value: new THREE.Texture() }
  }
});
composer.addPass(lut);

const tLookup = new THREE.TextureLoader().load('mercury.png');
tLookup.generateMipmaps = false;
tLookup.minFilter = THREE.LinearFilter;
lut.uniforms.tLookup.value = tLookup;

composer.passes[composer.passes.length - 1].renderToScreen = true;


createLoop(() => {
  composer.passes.forEach(pass => {
    if (pass.uniforms && pass.uniforms.resolution) {
      pass.uniforms.resolution.value.set(
        800, 450
      );
    }
  });
  composer.render();
}).start();


const playButton = document.getElementById('play');
playButton.onclick = () => video.play();
const stopButton = document.getElementById('pause');
stopButton.onclick = () => {
  video.pause();
};
// THREE END